.reviews {
    width: 711px;
    height: 133px;

    // position: absolute;
    // left: ((587px - 90px) - ((140px - 90px) / 1440px)) / 1440px * 100%;
    // bottom: ((146px - 90px) - ((140px - 90px) / 1440px)) / 1440px * 100%;
    .reviews_top {
        display: flex;
        margin-bottom: 10px;

        .photo {
            width: 48px;
            height: 48px;
            border-radius: 50%;

            // background: url('./../assets/image.svg') no-repeat;
            // background-position: -20px -10px;
            img {
                // margin: 0;
                width: 100%;
                height: 100%;
            }
        }

        p {
            margin-left: 14px;
            // display: inline-block;

            span {
                font-weight: bold;
            }
        }
    }

    .reviews_bottom {
        display: flex;

        // align-items: center;
        .like_button {

            // display: inline;
            #like_button_like {
                border: none;
                background-image: url('./../image/like_border.svg');
                background-position: -1px 0;
            }
        }

        .reviews_text {

            // display: inline;
            p {
                margin-left: 14px;
                // display: inline;
            }
        }
    }
}